.website {
    margin: 0 auto;
    width: 100%;
    max-width: 1280px;
        background: white;
}


.contenido .cotenido_general {
    font-size: 16px;
    padding: 50px 100px;
}

body {
    background: url(images/pattern.png) #207d8d;
    background-attachment: fixed;
    font-size: 16px;
    font-family: 'Open Sans', sans-serif;
}
.nav-tabs .nav-link{
  font-weight: bolder
}
.center{
  text-align: center;

}
.white{
  color: white
}
.btn-primary a{
