}
.tab-pane{
  color: black !important;
}

.nav-link {
     padding:0;
}
.nav-tabs .nav-item.show .nav-link, .nav-tabs .nav-link.active {
    color: #495057;
    border:none ;
}
.nav-tabs {
    border-bottom: none;
    cursor:pointer;
}
.nav-tabs .nav-link:focus, .nav-tabs .nav-link:hover,
.nav-tabs .nav-item:hover{
  border:none;
}
.nav-tabs .active{
   color: #ffa11f !important;
  border-bottom: 1px solid #ffa11f;
}

/*ACCORDING*/

.accordion__item + .accordion__item {
     border-top: none;
}
.accordion__title {
    background-color: #fff;

}

/*FOOTER*/

.footer {
    border-top: 1px solid #f2f3f8;
    padding: 33px 100px;
    background: #0a2735;
        color: #bbb;
}
.lower{

}


.footer .row .col-md-3 img {
    width: 100%;
}
.text_decreto img{
  max-height: 70px;
  width: auto !important;
}
.text_decreto{
  font-size: 12px;
}
.text_decreto a,
.text_footer a {
    color: #bbb
}
.infografico{
  cursor:pointer ;
}

.text_footer ul li {
    list-style: none;
    text-transform: uppercase;
    text-align: left;
}

.text_footer {
    text-align: justify;
    margin: 1% 6% 1% 5%;
    -ms-flex-pack: center;
        justify-content: center;
    font-size: 12px;
}
.text_decreto a,
.text_footer a:hover {
    color: #bbb;
    text-decoration: none
}

.text_footer .titulo {
    color: #fff;
