}
.one_text {
    text-align: center;
}

.text_footer ul {
    padding-left: 0;
}

.m-footer {
      text-align: center;
      background: white;
   }
 .m-footer img{
       width: 50%;
 }


/*FIN FOOTER*/


/*CAREOUSEL*/

.carousel-item img {
    width: 100%;
}

.carousel-indicators {
    bottom: -31px;
}
.carousel-indicators li {
    background-color: rgba(0, 0, 0, 1);
}
.carousel-indicators .active {
    background-color: red;
}
.carousel-control-prev,
.carousel-control-next{
  cursor:pointer ;
}
/*FIN CAROUSEL*/

/*NAVEGADOR*/
.navbar-light{
  background: #004861;
      padding: 1.4em;
}
.navbar-nav .active{
  color: #ffa11f !important;
  border-bottom: 1px solid #ffa11f;
}
.navbar-nav .active:hover{
  border-bottom: none;
}
.nav-item a{
  color: white;
  text-transform: uppercase;
  font-size: 14px;
}
.nav-item :hover{
  color: #ffa11f;
  border-bottom: 1px solid #ffa11f;
}
.nav-item a:hover{
  text-decoration: none
}
.nav-item{
      margin: 0px 10px;
}
.navbar-light .navbar-nav .nav-link:focus, .navbar-light .navbar-nav .nav-link:hover {
     color: #ffa11f
}

.navbar-light .navbar-nav .nav-link {
    color:white;
}
.navbar-brand{
      position: absolute;
    z-index: 1;
    top: -0.2em;
}
.navbar-brand img{
     width: 75%;
}
.inscribite{
      background: #ffa11f;
          border-radius: 4px;
              margin-left: 1em;
                  padding: 0.1em 1.5em;
}
.inscribite a{
      color: #00485f !important;
      font-weight: bold;

}
.inscribite a:hover{
      color: #00485f;
}
.Pop{
